*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
}
header{
    background-color: #F55A5A;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 16px;
    color: white;
    padding: 12px 0;
    margin-bottom: 32px;
}
header > i{
    font-size: 1.6rem;
}
main{
    padding: 18px 32px;
}
.section {
    padding: 20px 0;
    border-bottom: 1px solid #F5F5F5;
    display: flex;
    gap: 20px;
}
.section > img {
    border-radius: 8px;
    width: 150px;
    
    aspect-ratio: 1/1.25;
    object-fit: cover;
}
.section-info{
    align-self: center;
    flex-basis: 70%;
}
.country{
    display: flex;
    gap: 8px;
    align-items: center;
    color: #2B283A;
}
.country > i{
    color: #F55A5A;
}
.country > a{
    text-decoration: underline;
    font-size: 0.7rem;
    margin-top: 2px;
    color: #918E9B;
}
.location{
    margin-top: 20px;
}
.location-details{
    margin-top: 8px;
    line-height: 20px;
}